<template>
  <div id='app' class="hello">

     <div class="container">
       <div class="row padding-block">
          <div class="col-lg-4 offset-lg-1">

          <autocomplete :search="search"
           placeholder="Escolha a moeda base"
           aria-label="Escolha a moeda base"          
           :get-result-value="getResultValue" 
            @submit="handleSubmit"
            auto-select />

          </div>  
                    
       </div>
       <div class="row">
         <div class="col-lg-6 offset-lg-1">
              <div class="table-responsive">
                <table v-if="AllCoins" class="table table-stripped table-hover ">
                  <thead class="">
                     <tr>
                        <th>Sigla</th>
                        <th>Preço
                        <i class="fa fa-dollar-sign" ></i></th>
                     </tr>    
                  </thead>
                  <tbody>
                       <tr v-for="(k, v) in AllCoins" :key="v">
                          <td>{{v}}</td>  <td>{{k}}</td> 

                       </tr>
                  </tbody>
               </table>  
            </div>   
         </div>
       </div> 
     </div>



  </div>
</template>

<script>

import Autocomplete from '@trevoreyre/autocomplete-vue'
import { asyncLoading } from 'vuejs-loading-plugin'

export default {
  name: 'CurrencyCompareComponent',
  props: {
    msg: String
  },
  data:()=>({
    ratesList: [],
    AllCoins: null,
    crypto: {
      id: 0,
      symbol: '',
      websit_slug: ''
    }
  }),
  components: {
    Autocomplete
  },
  methods:{
    loadData(config)
    {      
        axios(config)
         .then(res => {
             this.ratesList = res.data.rates
           }).catch(err => console.log(err))
            
    },
     loadCoin(config)
    {      
       
       let result =  axios(config)
         .then(res =>  res.data)
            .catch(err => console.log(err))
          this.$loading(true)
           asyncLoading(result).then((res)=>{
                this.AllCoins =  res.rates
                this.AllCoins.base = res.base
                this.AllCoins.date = res.date
           }).catch(()=>{})
            
    },
    search(input) {
      if (input.length < 1) { return [] }
      return Object.keys(this.ratesList).filter((el, k)=> {
        return el.toLowerCase()
          .startsWith(input.toLowerCase())
      })
    },
    getResultValue(result) {
      return result
    },    
    handleSubmit(result) {  
    
      this.loadCoin({url: `/currency/${result}`})
    }
  },
  beforeMount(){
    
  },
  created() {
      this.loadData({url: '/currency'})
    
  }
}
</script>


